import React, { Fragment } from 'react';
import { Link, NavLink } from 'react-router-dom';
import '../assets/navbar.css';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';
import { getAllByLabelText } from '@testing-library/dom';

const Navbar = ({ logout, isAuthenticated }) => {
    const guestLinks = () => (
        <Fragment>
            <li className="nav-item">
                <Link className="nav-link hvr-glow active" to="/login" style={{borderRadius:"15px"}}>Login/Signup</Link>
            </li>
        </Fragment>
    );

    const authLinks = () => (
        <Fragment>
            <li className="nav-item">
                <a className="nav-link hvr-glow active" href="#!" onClick={logout} style={{borderRadius:"15px"}}>Logout</a>
            </li>
        </Fragment>
    );


return (
    <>
    <span></span>
    <div className="container-fluid m-0 p-0">
        <nav className="navbar navbar-expand-xl navbar-dark m-30 p-10 clearfix" id="nav-colors">
        <button className="navbar-toggler" type="button" data-toggle="collapse"  data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto" style={{position:"relative", zIndex:'99'}}>
                <li className="nav-item active mr-3">
                    <Link className="nav-link hvr-glow" to="/"><i className="fas fa-tree border border-success rounded-circle" style={{color: 'darkgreen'}} /></Link>
                </li>
            <li className="nav-item active">
                <Link className="nav-link hvr-glow mr-5" to="/api/blog/about" style={{borderRadius:"15px"}}> 
                About Us <span className="sr-only">(current)</span></Link>
            </li>
            <br/>
            <li className="nav-item active">
                <Link className="nav-link hvr-glow mr-5" 
                to="/api/survey/treeSurvey/" style={{borderRadius:"15px"}}>
                Surveys
                <span className="sr-only">(current)</span>
                </Link>
            </li>
            <li className="nav-item active">
                <Link className="nav-link hvr-glow mr-5" to="/api/blog/bloglist/" style={{borderRadius:"15px"}}> 
                Blog <span className="sr-only">(current)</span></Link>
            </li>
            <li className="nav-item active">
                <Link to='/api/blog/contact' className="nav-link hvr-glow mr-5" href="#" style={{borderRadius:"15px"}}>Contact Us</Link>
            </li>
            {isAuthenticated ?  authLinks() : guestLinks()}
            </ul>
            <form className="form-inline my-2 my-lg-0">
            <input className="form-control mr-md-2 active mr-5" type="search" placeholder="Search" style={{borderRadius:"15px"}} aria-label="Search"></input>
            <button className="hvr-glow btn btn-outline-success" style={{borderRadius:"15px"}} type="submit"><label getAllByLabelText>Search</label></button>
            </form>
            
        </div>
        </nav>
        </div>
    </>
)
};


const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});
export default connect(mapStateToProps, { logout })(Navbar);