import React from 'react';
import { Link } from 'react-router-dom';

const BlogContact = () => (


    <div className="container-fluid text-center">
            <div className="container col-md-8 col-lg-8 mt-5 bg-dark" style={{ borderRadius:"25px",
            height:"600px",
            backgroundSize: "100%",
            alignItems:"center",
            background:"darkgreen"}}>
                <h1 className="display-2" style={{ marginRight:"10px", color: 'white', border:'none', padding:'0px'}}>Contact Us</h1>
                <h6 className="display-4"></h6>
                <div className='form-group active'>
                    <label>Your Name</label>
                    <input
                    type='text' 
                    className='form-control' 
                    placeholder='Your Name' 
                    />
                </div>
                <div className='form-group'>
                    <label>Your Contact</label>
                    <input type='text' 
                    className='form-control' 
                    placeholder='Contact Number' 
                    />
                </div>
                <div className='form-group'>
                    <label>Email ID</label>
                    <input type='text' 
                    className='form-control' 
                    placeholder='Email ID' 
                    />
                </div>
                <div className='form-group'>
                    <label>Subject of Service</label>
                    <input type='text' 
                    className='form-control' 
                    placeholder='What can we do for you?' 
                    />
                </div>
                <div className='form-group'>
                    <label>Message</label>
                    <input type='text' 
                    className='form-control' 
                    placeholder="Message"
                    height="200px"
                    />
                </div>
                </div>    
            </div>  

    );

export default BlogContact;